.circlebutton {
  width: 25px; /* ボタンの幅 */
  height: 25px; /* ボタンの高さ */
  background-color: #fff; /* ボタンの背景色 */
  border: 2px solid #ddd; /* ボタンの境界線 */
  border-radius: 50%; /* 円形にするために50%のボーダー半径 */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px; /* テキストのサイズ */
  color: #888; /* テキストの色 */
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); /* 影 */
  transition: background-color 0.3s, border-color 0.3s; /* ホバー時のトランジション */
  margin-left: 10px;
}

.circlebutton:hover {
  background-color: #f8f8f8; /* ホバー時の背景色 */
  border-color: #ccc; /* ホバー時の境界線の色 */
}

.menu {
  position: absolute;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding: 10px;
  margin-top: 10px;
  z-index: 100;
}

.menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu li {
  padding: 5px 10px;
  cursor: pointer;
}

.menu li:hover {
  background-color: #f0f0f0;
}

.version {
  font-family: "Google Sans", "Roboto", Helvetica;
  font-weight: 200;
  margin-right: 10px;
  color: #888;
}

.helpmenu{
  font-family: "Google Sans","Roboto",Helvetica; 
  font-weight: 400;
  text-decoration: none;
  color: inherit;
}

.table_container {
  display: inline-block;
  position: absolute;
  border: 1px transparent;
  touch-action: none; /* Prevent default touch actions */
}

.resizer {
  width: 20px;
  height: 20px;
  background: transparent;
  position: absolute;
  right: 0;
  bottom: 0;
  cursor: nwse-resize;
  touch-action: none; /* Prevent default touch actions */
}
